html,
body,
section {
  min-block-size: 100vh;
}

html {
  scroll-snap-type: y mandatory;
}

section {
  scroll-snap-align: start;
  scroll-snap-stop: always;
  display: flex;
  color: var(--darkgrey-color);
  margin-left: 15rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#accueil {
  margin: 0;
}

